import { HttpClient } from '@angular/common/http';
import 'rxjs/add/operator/map';
var GetSpellsService = /** @class */ (function () {
    function GetSpellsService(http) {
        this.http = http;
        this.baseURL = 'http://dnd5eapi.co/api/';
    }
    GetSpellsService.prototype.getWizardSpells = function () {
        return this.http.get('http://dnd5eapi.co/api/spells/wizard/').subscribe(function (result) {
            console.log(result);
        }, function (error) {
            console.log('Can\'t get spells');
        });
    };
    return GetSpellsService;
}());
export { GetSpellsService };



